@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhaijaan+2:wght@457&display=swap);

* {
  font-family: "Baloo Bhaijaan 2", cursive;
  list-style: none;
}
body {
  background-color: rgb(10, 10, 10);
  background-image: url(https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/e4996759-6e2a-41d1-bc8b-8d38cf9de843/d1nwdwd-d59173ab-6875-4780-97ff-56a49fc4060a.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..SJfmi-6kPwVypfpP1O4i6zfp9VFjQV5v376bbEMyRpU);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: white;
}

.chat-selector {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-height: 100%;
  width: 99%;
}
.containers-container {
  width: 80%;
  height: 100%;
}
.container {
  border-bottom: 0.5px solid rgb(255, 255, 255);
  border-top: 0.5px solid rgb(252, 252, 252);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  padding-left: 50px;
  cursor: pointer;
}
.group-one {
  display: flex;
}
.delete-chat {
  z-index: 1;
  padding-top: 3px;
  width: 20px;
  height: 20px;
  border: 2px solid grey;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  bottom: 70px;
  left: 100%;
}

.delete-chat:hover {
  background-color: red;
  cursor: pointer;
}
#chat-list-names {
  text-transform: capitalize;
}

.complete-container {
  height: 80vh;
  overflow-y: scroll;
}
header {
  display: flex;
  justify-content: right;
  align-items: flex-end;
  height: 100px;
  padding: 20px;
}
.search-container {
  display: inline-flex;
  flex-direction: column;
  max-height: 100px;
  overflow-y: scroll;
}
.logo {
  width: 50%;
  font-size: 25px;
  position: absolute;
  top: 30px;
  right: 100px;
}
.searching-bar {
  width: 82vw;
  background-color: grey;
  border: none;
  border-radius: 10px;
  text-align: center;
  height: 30px;
}
.search-results {
  background-color: rgb(73, 73, 73);
  width: 79vw;
  position: relative;
  left: 8px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  padding-left: 20px;
  padding-top: 10px;
}
.search-div {
  display: flex;
  justify-content: center;
}

.messageBox {
  margin-top: 100px;
  margin-bottom: 40px;
  max-width: 100%;
  max-height: 80px;
  display: flex;
  justify-content: right;
}
.field {
  margin-top: 10px;
  margin-bottom: 10px;
}
.message-box-container {
  display: flex;
  flex-direction: column;
  max-height: 70px;
  align-items: flex-start;
  justify-content: space-around;
  background-color: #69b3c4;
  border-radius: 20px;
  padding: 20px;
}
.otherUser {
  background-color: #4e4e4e;
}
.other {
  justify-content: left;
}
.background {
  padding: 20px;
  height: 100vh;
  overflow: scroll;
  margin-bottom: 50px;
}
.message-form {
  background-color: #98c4cd;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
.message-submit {
  width: 80vw;
  border-radius: 20px;
  border: none;
  text-align: center;
  height: 35px;
}
.back-button {
  background-color: #d7f8ff;
  padding: 10px;
  border-radius: 10px;
  position: fixed;
}
.back-button:hover {
  cursor: pointer;
}
.holder {
  display: flex;
  justify-content: center;
  align-items: center;
}
.title {
  text-align: center;
  font-size: 40px;
}
.submit-button {
  margin-bottom: 20px;
  padding: 10px;
  background-color: #d7f8ff;
  width: 100px;
  border: 2px solid #98c4cd;
  border-radius: 10px;
}
.submit-button:hover {
  background-color: #a5e9f1;
}
.sub-hold {
  display: flex;
  justify-content: center;
  width: 100%;
}
.circle {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-right: 30px;
  height: 70px;
  background-color: #98c4cd;
  border: 3px solid #85e4f7;
  width: 70px;
  border-radius: 50px;
}
#chat-list-names {
  position: relative;
  top: 10px;
}

.delete-box {
  color: red;
  position: relative;
  left: 5px;
  top: 10px;
}
.other-delete {
  display: none;
}
.delete-box p:hover {
  color: rgb(139, 4, 4);
  cursor: pointer;
}

